/* Kanban Board Main Container */
.kanban-board {
  width: 100%;
  max-width: 100%;
  min-height: 400px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  padding: 20px;
  box-sizing: border-box;
  border-radius: 12px;
  position: relative;
  overflow: hidden; /* Ensure content stays within container */
}

/* Kanban Columns Container - WITH HORIZONTAL SCROLLING */
.kanban-columns {
  display: flex;
  gap: 16px;
  height: 100%;
  width: 100%;
  max-width: 100%;
  overflow-x: auto; /* Allow horizontal scrolling */
  overflow-y: hidden;
  padding-bottom: 15px; /* Extra space for horizontal scrollbar */
  box-sizing: border-box;
}

/* Individual Column - RESPONSIVE WIDTH WITH HORIZONTAL SCROLL */
.kanban-column {
  flex: 1; /* Equal width for all columns */
  min-width: 250px; /* Minimum column width for readability */
  max-width: 350px; /* Maximum column width to prevent too wide columns */
  display: flex;
  flex-direction: column;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  border: 1px solid #e0e0e0;
  transition: all 0.2s ease-in-out;
  box-sizing: border-box;
}

.kanban-column:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.15);
}

/* Column Header */
.kanban-column-header {
  padding: 16px 20px;
  color: white;
  font-weight: 600;
  font-size: 14px;
  text-align: center;
  border-radius: 12px 12px 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  min-height: 50px;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

/* Column Body */
.kanban-column-body {
  flex: 1;
  padding: 16px;
  overflow-y: auto;
  overflow-x: hidden;
  height: 0; /* Force flex to calculate height */
  display: flex;
  flex-direction: column;
  gap: 12px;
  min-height: 200px;
  box-sizing: border-box;
}

/* Kanban Cards - WITH SOURCE-BASED BACKGROUND */
.kanban-card {
  border-radius: 10px;
  border: 1.5px solid #e0e0e0;
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
  padding: 18px 16px 12px 16px;
  margin-bottom: 12px;
  transition: all 0.2s ease-in-out;
  font-size: 15px;
  cursor: pointer;
}

/* Default card background */
.kanban-card {
  background: #fff;
}

/* Source-specific card backgrounds */
.kanban-card[data-source="VV"] {
  background: rgba(25, 118, 210, 0.08); /* Light blue for VV */
  border-color: rgba(25, 118, 210, 0.2);
}

.kanban-card[data-source="OF"] {
  background: rgba(255, 140, 0, 0.08); /* Light orange for OF */
  border-color: rgba(255, 140, 0, 0.2);
}

.kanban-card:hover {
  box-shadow: 0 8px 24px rgba(52, 152, 219, 0.15);
  border-color: #3498db;
  transform: translateY(-2px);
}

.kanban-card.active {
  opacity: 0.8;
  transform: rotate(2deg);
  z-index: 1000;
}

.kanban-card.overlay {
  transform: rotate(5deg);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
  z-index: 1001;
}

/* Card Header */
.kanban-card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 8px;
}

.kanban-card-title {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
  color: #2c3e50;
  line-height: 1.3;
  flex: 1;
}

.kanban-card-priority {
  margin-left: 8px;
}

.priority-icon {
  font-size: 14px;
}

/* Card Content */
.kanban-card-content {
  margin-bottom: 12px;
}

.kanban-card-content p {
  margin: 0;
  font-size: 14px;
  color: #666;
  line-height: 1.4;
}

/* Card Footer */
.kanban-card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  font-size: 12px;
  color: #888;
}

.kanban-card-assignee,
.kanban-card-date {
  display: flex;
  align-items: center;
  gap: 4px;
}

.assignee-avatar,
.date-icon {
  font-size: 12px;
}

.assignee-name,
.date-text {
  font-size: 11px;
}

/* Card Tags */
.kanban-card-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  margin-top: 8px;
}

.kanban-card-tag {
  display: inline-block;
  background: #e3f2fd;
  color: #1976d2;
  border-radius: 10px;
  padding: 2px 8px;
  font-size: 12px;
  font-weight: 500;
  margin-right: 4px;
  margin-bottom: 2px;
}

.kanban-card-tag-more {
  background: #f5f5f5;
  color: #666;
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 10px;
  font-weight: 500;
}

/* Custom HTML Utility Classes for Deal Cards */
.priority-high {
  background-color: #fef2f2;
  color: #dc2626;
  font-size: 11px;
  font-weight: 600;
  padding: 4px 8px;
  border-radius: 4px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border: 1px solid #fecaca;
  display: inline-block;
}

.priority-medium {
  background-color: #fefbf2;
  color: #d97706;
  font-size: 11px;
  font-weight: 600;
  padding: 4px 8px;
  border-radius: 4px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border: 1px solid #fed7aa;
  display: inline-block;
}

.priority-low {
  background-color: #f0fdf4;
  color: #16a34a;
  font-size: 11px;
  font-weight: 600;
  padding: 4px 8px;
  border-radius: 4px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border: 1px solid #bbf7d0;
  display: inline-block;
}

.status-urgent {
  background-color: #450a0a;
  color: #fef2f2;
  font-size: 11px;
  font-weight: 600;
  padding: 4px 8px;
  border-radius: 4px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  display: inline-block;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  animation: modalSlideIn 0.3s ease-out;
}

.modal-content {
  background: white;
  border-radius: 12px;
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-30px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.modal-header {
  padding: 24px 24px 16px;
  border-bottom: 1px solid #e0e0e0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h2 {
  margin: 0;
  color: #2c3e50;
  font-size: 24px;
  font-weight: 600;
}

.modal-close {
  background: none;
  border: none;
  font-size: 28px;
  color: #999;
  cursor: pointer;
  padding: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.2s ease;
}

.modal-close:hover {
  background: #f0f0f0;
  color: #666;
}

.modal-body {
  padding: 20px 24px 24px;
}

.card-section {
  margin-bottom: 20px;
}

.card-section h3 {
  margin: 0 0 8px 0;
  color: #2c3e50;
  font-size: 16px;
  font-weight: 600;
}

.card-section p {
  margin: 0;
  color: #666;
  line-height: 1.6;
}

.priority-badge {
  color: white;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.tags-container {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin-top: 8px;
}

.tag {
  background: #e3f2fd;
  color: #1976d2;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
}

.card-raw-data {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 6px;
  padding: 12px;
  font-size: 12px;
  color: #495057;
  white-space: pre-wrap;
  overflow-x: auto;
  max-height: 200px;
  overflow-y: auto;
}

/* Droppable styling */
.kanban-column[data-droppable="true"] {
  background-color: rgba(52, 152, 219, 0.1);
}

.kanban-column[data-droppable="true"] .kanban-column-body {
  background-color: rgba(52, 152, 219, 0.05);
  border: 2px dashed #3498db;
  border-radius: 8px;
}

/* Responsive design with horizontal scrolling */
@media (max-width: 1200px) {
  .kanban-column {
    min-width: 240px;
    max-width: 320px;
  }
}

@media (max-width: 992px) {
  .kanban-column {
    min-width: 220px;
    max-width: 300px;
  }
}

@media (max-width: 768px) {
  .kanban-board {
    padding: 12px;
  }

  .kanban-columns {
    gap: 12px;
    overflow-x: auto;
    overflow-y: hidden;
    padding-bottom: 20px;
  }

  .kanban-column {
    min-width: 280px;
    max-width: 320px;
    flex: 0 0 280px; /* Fixed width for horizontal scrolling */
  }

  .kanban-column-body {
    max-height: 400px;
  }

  .modal-content {
    margin: 20px;
    width: calc(100% - 40px);
  }

  .modal-header {
    padding: 20px 20px 12px;
  }

  .modal-body {
    padding: 16px 20px 20px;
  }
}

@media (max-width: 480px) {
  .kanban-board {
    padding: 10px;
  }
  
  .kanban-columns {
    gap: 8px;
  }
  
  .kanban-column {
    min-width: 260px;
    max-width: 280px;
    flex: 0 0 260px; /* Fixed width for horizontal scrolling */
  }
  
  .kanban-column-body {
    padding: 12px;
  }
}

/* Scrollbar styling */
.kanban-column-body::-webkit-scrollbar,
.modal-content::-webkit-scrollbar,
.card-raw-data::-webkit-scrollbar {
  width: 8px;
}

.kanban-column-body::-webkit-scrollbar-track,
.modal-content::-webkit-scrollbar-track,
.card-raw-data::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.kanban-column-body::-webkit-scrollbar-thumb,
.modal-content::-webkit-scrollbar-thumb,
.card-raw-data::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
}

.kanban-column-body::-webkit-scrollbar-thumb:hover,
.modal-content::-webkit-scrollbar-thumb:hover,
.card-raw-data::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Horizontal scrollbar styling for columns container */
.kanban-columns::-webkit-scrollbar {
  height: 8px;
}

.kanban-columns::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.kanban-columns::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
  transition: background 0.2s ease;
}

.kanban-columns::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Additional field styling from the original */
.kanban-card-field-label {
  font-weight: 600;
  color: #666;
  margin-right: 4px;
}

.kanban-card-field-bold {
  font-weight: 600;
}

.kanban-card-badge {
  display: inline-block;
  background: #e74c3c;
  color: white;
  font-size: 11px;
  font-weight: 600;
  padding: 3px 8px;
  border-radius: 12px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 6px;
}

.kanban-card-badge-label {
  font-weight: 500;
  margin-right: 4px;
}

.kanban-card-badge-value {
  font-weight: 600;
}

.kanban-card-date-label {
  font-weight: 500;
  margin-right: 4px;
}

.kanban-card-date .date-text {
  font-weight: 500;
}

/* Drag and drop states */
.kanban-column.drag-over {
  background-color: rgba(52, 152, 219, 0.08);
  border: 2px dashed #3498db;
  transform: scale(1.02);
}

.kanban-card.dragging {
  opacity: 0.6;
  transform: rotate(3deg) scale(1.05);
  cursor: grabbing;
  z-index: 1000;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.kanban-card[draggable="true"] {
  cursor: grab;
  transition: all 0.2s ease-in-out;
}

.kanban-card[draggable="true"]:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.kanban-column {
  transition: all 0.2s ease-in-out;
}

.kanban-card {
  transition: all 0.2s ease-in-out;
}

/* Permission-based styling */
.drop-disabled {
  background-color: rgba(220, 53, 69, 0.1) !important;
  border: 2px dashed #dc3545 !important;
}

.drop-disabled .kanban-column-header {
  background-color: #dc3545 !important;
  color: white !important;
}

.kanban-card.not-draggable {
  cursor: not-allowed !important;
  opacity: 0.7;
  position: relative;
}

.kanban-card.not-draggable::after {
  content: "🔒";
  position: absolute;
  top: 8px;
  right: 8px;
  font-size: 12px;
  opacity: 0.6;
}

.kanban-card.not-draggable:hover {
  transform: none !important;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1) !important;
}

.lock-icon {
  font-size: 14px;
  margin-left: 8px;
  opacity: 0.9;
} 